/* Basic styles for the Kanban board layout */
.kanban-board {
    padding: 20px;
    text-align: center;
  }
  
  .kanban-columns {
    display: flex;
    justify-content: space-around;
    gap: 20px;
    margin-top: 20px;
  }
  
  .kanban-column {
    background-color: #f0f0f5;
    padding: 10px;
    border-radius: 8px;
    width: 250px;
  }
  
  .new-task-button {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 5px 10px;
    cursor: pointer;
    border-radius: 5px;
    margin-bottom: 10px;
  }
  
  .task-list {
    margin-top: 10px;
  }
  
  .task-item {
    background-color: #ffffff;
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    position: relative;
  }
  
  .task-title, .task-description {
    width: 100%;
    border: none;
    margin: 5px 0;
    padding: 5px;
    background-color: #f9f9f9;
    border-radius: 3px;
  }
  
  .task-title:focus, .task-description:focus {
    outline: none;
    background-color: #e9e9ff;
  }
  
  .delete-task {
    position: absolute;
    top: 5px;
    right: 5px;
    background: none;
    border: none;
    font-size: 1.2em;
    color: #ff5c5c;
    cursor: pointer;
  }
  